<template>
  <el-row :style="content">
    <el-col :lg="{ span: 16, offset: 3 }" :sm="17" :xs="17">
      <div v-for="(value, index) in allRmdDocs" :key="index" v-if="showChunk">
        <chunk v-if="value.isShow" :chunk="value"></chunk>
      </div>
    </el-col>
    <el-col :lg="5" :sm="7" :xs="7">
      <personal-info></personal-info>
      <div
        v-for="(value, index) in allLatestDocs"
        :key="index"
        v-if="showLatest"
      >
        <latest :latest="value" v-if="value.isShow"></latest>
      </div>
    </el-col>
  </el-row>
</template>

<script>
import axios from "axios";
import PersonalInfo from "../PersonalInfo";
import Chunk from "./Chunk";
import Latest from "./Latest";
import settigns from "../../plugins/settings";
var apiUrl = settigns.url.api;

export default {
  name: "index-content",
  data() {
    return {
      content: {
        margin: "5px",
        marginTop: "20px",
      },
      allRmdDocs: settigns.index.allRmdDocs,
      allRmdCount: 0,
      getRmdCount: 0,
      showChunk: false,
      allLatestDocs: settigns.index.allLatestDocs,
      allLatestCount: 0,
      getLatestCount: 0,
      showLatest: false,
    };
  },
  components: {
    Chunk,
    PersonalInfo,
    Latest,
  },
  methods: {
    zfill(number) {
      return number.toString().padStart(2, "0");
    },
    buildWeek(weekday) {
      const week = [
        "星期一",
        "星期二",
        "星期三",
        "星期四",
        "星期五",
        "星期六",
        "星期日",
      ];
      return week[weekday];
    },
    buildLaunchTime(item) {
      const launchTime =
        `${item.year}-${this.zfill(item.month)}-${this.zfill(item.day)}` +
        ` ${this.zfill(item.hour)}:${this.zfill(item.minute)}:${this.zfill(
          item.second
        )}` +
        ` ${this.buildWeek(item.weekday)}` +
        ` No.${item.yearday}Day@${item.year}`;
      return `发布于:${launchTime}`;
    },
    buildChunkData(docType, res) {
      var data = this.allRmdDocs[docType];
      data.cards = [];
      for (var i = 0; i < res.length; i++) {
        data.cards.push({
          uuid: res[i].uuid,
          title: res[i].title,
          subtitle: this.buildLaunchTime(res[i]),
          text: res[i].text,
          docType,
        });
      }
      this.allRmdDocs[docType] = data;
    },
    getRmdDocs() {
      this.allRmdCount = Object.keys(this.allRmdDocs).length;
      var docType = "";
      for (docType in this.allRmdDocs) {
        this.getRmdDoc(docType);
      }
    },
    getRmdDoc(docType) {
      axios
        .post(`${apiUrl}doc/getRmdDoc`, {
          docType,
        })
        .then((res) => {
          if (res.data.length !== 0) {
            this.allRmdDocs[docType].isShow = true;
            this.buildChunkData(docType, res.data);
          }
          this.getRmdCount += 1;
          if (this.getRmdCount >= this.allRmdCount) {
            this.showChunk = true;
          }
        });
    },
    getLatestDocs() {
      this.allLatestCount = Object.keys(this.allLatestDocs).length;
      var docType = "";
      for (docType in this.allLatestDocs) {
        this.getLatestDoc(docType);
      }
    },
    getLatestDoc(docType) {
      axios
        .post(`${apiUrl}doc/getLatestDoc`, {
          docType,
        })
        .then((res) => {
          if (res.data.length !== 0) {
            var data = this.allLatestDocs[docType];
            data.isShow = true;
            data.uuid = res.data[0].uuid;
            data.title = res.data[0].title;
            this.allLatestDocs[docType] = data;
          }
          this.getLatestCount += 1;
          if (this.getLatestCount >= this.allLatestCount) {
            this.showLatest = true;
          }
        });
    },
  },
  mounted() {
    this.getRmdDocs();
    this.getLatestDocs();
  },
};
</script>
