<template>
  <div :style="coverStyle">
    <div class="cover">
      <span class="cover-title">{{ doc.title }}</span>
      <span class="cover-time">发布于📆:{{ launchTime }}</span>
      <span class="cover-weather">天气:{{ doc.weather }}</span>
    </div>
  </div>
</template>

<script>
import settings from "../../plugins/settings";
var fileUrl = settings.url.file;

export default {
  name: "docuement-cover",
  data() {
    return {
      launchTime: "",
      coverStyle: {
        height: "40vh",
        backgroundImage: "url()",
        backgroundSize: "cover",
        backgroundPosition: "center 20%",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
      },
    };
  },
  props: ["doc"],
  methods: {
    buildWeek(weekday) {
      const week = [
        "星期一",
        "星期二",
        "星期三",
        "星期四",
        "星期五",
        "星期六",
        "星期日",
      ];
      return week[weekday];
    },
    buildSrc(root) {
      return `${fileUrl}${root}`;
    },
    buildCoverBackground() {
      const coverUrl = `${fileUrl}${this.doc.cover}`;
      this.coverStyle.backgroundImage = `url(${coverUrl})`;
    },
    buildLaunchTime() {
      this.launchTime =
        `${this.doc.year}-${this.zfill(this.doc.month)}-${this.zfill(
          this.doc.day
        )}` +
        ` ${this.zfill(this.doc.hour)}:${this.zfill(
          this.doc.minute
        )}:${this.zfill(this.doc.second)}` +
        ` ${this.buildWeek(this.doc.weekday)}` +
        ` No.${this.doc.yearday}Day@${this.doc.year}`;
    },
    zfill(number) {
      return number.toString().padStart(2, "0");
    },
  },
  mounted() {
    this.buildCoverBackground();
    this.buildLaunchTime();
  },
};
</script>

<style scoped>
.cover {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.cover-title {
  display: block;
  font-size: 200%;
  color: azure;
  text-align: center;
  text-shadow: 1px 1px 10px #777777;
}
.cover-time {
  display: block;
  font-size: 80%;
  color: azure;
  text-align: center;
  text-shadow: 1px 1px 10px #777777;
}

.cover-weather {
  display: block;
  font-size: 80%;
  color: azure;
  text-align: center;
  text-shadow: 1px 1px 10px #777777;
}
</style>