import {
    Row,
    Col,
    Select,
    Option,
    Divider,
    Timeline,
    TimelineItem,
    Pagination,
    Image
} from 'element-ui'

const element = {
    install: function (Vue) {
        Vue.use(Row)
        Vue.use(Col)
        Vue.use(Select)
        Vue.use(Option)
        Vue.use(Divider)
        Vue.use(Timeline)
        Vue.use(TimelineItem)
        Vue.use(Pagination)
        Vue.use(Image)
    }
}

export default element