<template>
  <div>
    <div id="scrollDown">
      <img src="/static/down.png" id="down" @click="scroll"></img>
    </div>
    <index-cover></index-cover>
    <index-content></index-content>
    <records></records>
  </div>
</template>

<script>
import IndexCover from "../components/index/Cover";
import IndexContent from "../components/index/Content";
import Records from "../components/Records";
import Bezier from "../plugins/bezier.js";
var dom = document.documentElement;

export default {
  name: "index",
  components: { IndexCover, IndexContent, Records },
  methods: {
    scroll() {
      const steps = 48;
      var oldTop = dom.scrollTop;
      var targetTop = document.body.clientHeight;
      var moveTop = targetTop - oldTop;
      var bez = Bezier.getBezierPoints(
        steps,
        [0, 0],
        [0.8, 0.2],
        [0.2, 0.8],
        [1, 1]
      );
      const sec = 0.8;
      var step = 0;
      var inter = setInterval(() => {
        dom.scrollTop = oldTop + bez[step][1] * moveTop;
        step += 1;
        if (step > steps) {
          clearInterval(inter);
        }
      }, (sec * 1000) / steps);
    },
  },
};
</script>

<style scoped>
#scrollDown {
  position: absolute;
  width: 100%;
  height: 80vh;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  animation: up-down 1s infinite;
}
#down {
  width: 48px;
}
@keyframes up-down {
  0% {
    height: 85vh;
  }
  50% {
    height: calc(85vh + 10px);
  }
  100% {
    height: 85vh;
  }
}
</style>