<template>
  <el-row :style="content">
    <el-col :lg="{ span: 16, offset: 3 }" :sm="17" :xs="17">
      <div style="margin: 10px">
        <el-select v-model="docType" placeholder="文档种类" @change="getDocs">
          <el-option
            v-for="(item, index) in docTypes"
            :key="index"
            :value="item"
            :label="translate(item)"
          ></el-option>
        </el-select>
        <el-divider></el-divider>
        <el-timeline>
          <el-timeline-item
            v-for="(item, index) in docs"
            :key="index"
            :timestamp="buildLaunchTime(item)"
            placement="top"
          >
            <div class="card">
              <router-link :to="buildTo(item)">
                <span class="card-title">{{ item.title }}</span>
              </router-link>
              <span class="card-text">{{ item.text }}</span>
            </div>
          </el-timeline-item>
        </el-timeline>
        <el-pagination
          background
          :current-page="currentPage"
          :page-count="pageCount"
        ></el-pagination>
      </div>
    </el-col>
    <el-col :lg="5" :sm="7" :xs="7">
      <personal-info></personal-info>
    </el-col>
  </el-row>
</template>

<script>
import axios from "axios";
import settings from "../../plugins/settings";
import PersonalInfo from "../PersonalInfo";
var apiUrl = settings.url.api;

export default {
  name: "catalogue-content",
  data() {
    return {
      content: {
        margin: "20px 5px 5px 5px",
      },
      docTypes: [],
      docType: "",
      docs: [],
      currentPage: 1,
      pageCount: 1,
    };
  },
  components: {
    PersonalInfo,
  },
  methods: {
    translate(value) {
      const translate = settings.catalogue.translate;
      if (value in translate) {
        return translate[value];
      } else {
        return value;
      }
    },
    zfill(number) {
      return number.toString().padStart(2, "0");
    },
    buildWeek(weekday) {
      const week = [
        "星期一",
        "星期二",
        "星期三",
        "星期四",
        "星期五",
        "星期六",
        "星期日",
      ];
      return week[weekday];
    },
    buildLaunchTime(item) {
      const launchTime =
        `${item.year}-${this.zfill(item.month)}-${this.zfill(item.day)}` +
        ` ${this.zfill(item.hour)}:${this.zfill(item.minute)}:${this.zfill(
          item.second
        )}` +
        ` ${this.buildWeek(item.weekday)}` +
        ` No.${item.yearday}Day@${item.year}`;
      return `发布于:${launchTime}`;
    },
    getTypes() {
      axios.post(`${apiUrl}doc/getTypes`, {}).then((res) => {
        this.docTypes = res.data;
        this.docType = res.data[0];
        this.getDocs();
      });
    },
    getDocs() {
      axios
        .post(`${apiUrl}doc/getDocs`, {
          docType: this.docType,
          currentPage: this.currentPage,
          title: "",
        })
        .then((res) => {
          this.docs = res.data.rows;
          this.pageCount = res.data.pageCount;
        });
    },
    buildTo(item) {
      return `/doc/${this.docType}/${item.uuid}`;
    },
  },
  mounted() {
    this.getTypes();
  },
};
</script>

<style scoped>
.left {
  margin: 0px 15px 15px 15px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.select-doc {
  display: flex;
  flex-direction: column;
}
.card {
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  padding: 10px;
  background-color: #777777;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
  color: azure;
}
.card-title {
  font-size: 120%;
  font-weight: bolder;
}
.card-text {
  margin-top: 5px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
a {
  text-decoration: none;
  color: azure;
}
</style>