<template>
  <div class="card">
    <span class="card-title">{{ mainTitle }}</span>
    <span class="card-text">👉{{ title }}</span>
  </div>
</template>

<script>
export default {
  name: "latest",
  props: ["latest"],
  data() {
    return {
      uuid:"",
      mainTitle: "",
      title: "",
    };
  },
  mounted() {
    this.uuid = this.latest.uuid
    this.mainTitle = this.latest.mainTitle
    this.title = this.latest.title
  },
};
</script>

<style scoped>
.card {
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  padding: 10px;
  background-color: #777777;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
  color: azure;
  margin-top: 10px;
}
.card-title {
  display: block;
  font-size: 100%;
  font-weight: bolder;
}
.card-text {
  display: block;
  font-size: 80%;
  margin-top: 10px;
}
</style>