import VueRouter from 'vue-router'
import index from "../views/index"
import catalogue from '../views/catalogue'
import document from '../views/document'

export default new VueRouter(
  {
    routes: [
      {
        path: '/',
        component: index
      },
      {
        path: '/catalogue',
        component: catalogue
      },
      {
        path: '/doc/:docType/:uuid',
        component: document
      }
    ],
    mode:"history"
  }
)