import Vue from 'vue'
import VueRouter from 'vue-router'
import App from './App.vue'
import router from './router'
import 'element-ui/lib/theme-chalk/index.css';
import element from './plugins/element'

Vue.config.productionTip = false
Vue.use(VueRouter)
Vue.use(element)

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
