<template>
  <div class="cover">
    <span class="cover_title">{{ coverTitle }}</span>
    <div class="greet">
      <span id="greet">{{ greetText }}</span>
      <span id="underline-blink">_</span>
    </div>
  </div>
</template>

<script>
import Settings from "../../plugins/settings";

export default {
  name: "index-cover",
  data() {
    return {
      coverTitle: Settings.index.coverTitle,
      greetText: "",
      text: "",
    };
  },
  computed: {
    text_length() {
      return this.text.length;
    },
  },
  methods: {
    greeting() {
      var i = 0;
      var inter = setInterval(() => {
        i += 1;
        this.greetText = this.text.slice(0, i);
        if (i >= this.text.length) {
          clearInterval(inter);
        }
      }, 200);
    },
    getNoon() {
      const d = new Date();
      const hour = d.getHours();
      if (hour >= 6 && hour < 8) {
        this.text = "哟!早上好呀,吃了吗?";
      } else if (hour >= 8 && hour < 11) {
        this.text = "哟!上午好呀,吃了吗?";
      } else if (hour >= 11 && hour < 13) {
        this.text = "哟!中午好呀,吃了吗?";
      } else if (hour >= 13 && hour < 17) {
        this.text = "哟!下午好呀,吃了吗?";
      } else if (hour >= 17 && hour < 22) {
        this.text = "哟!晚上好呀,吃了吗?";
      } else if ((hour >= 22 && hour < 24) || (hour >= 0 && hour < 4)) {
        this.text = "哟!深夜好呀,吃了吗?";
      } else {
        this.text = "哟!凌晨好呀,吃了吗?";
      }
    },
  },
  mounted() {
    this.getNoon();
    this.greeting();
  },
};
</script>

<style scoped>
.cover {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100vh;
  justify-content: center;
  background-image: url("../../assets/indexCover.jpg");
  background-position: center;
  background-size: cover;
}

.cover span {
  height: min-content;
  color: azure;
  text-shadow: 2px 2px 5px #777777;
  text-align: center;
}

.greet {
  display: flex;
  justify-content: center;
  font-size: 150%;
  margin-top: 20px;
  font-weight: lighter;
}

.cover_title {
  font-size: 300%;
  font-weight: bold;
}

#underline-blink {
  animation: blink 1s cubic-bezier(1, 0, 0, 1) infinite;
}

@keyframes blink {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}
</style>