<template>
  <div v-if="isShow">
    <document-cover :doc="doc"></document-cover>
    <document-content :doc="doc"></document-content>
  </div>
</template>

<script>
import axios from "axios";
import DocumentCover from "../components/Document/Cover";
import DocumentContent from "../components/Document/Content";
import settings from "../plugins/settings";
var apiUrl = settings.url.api;

export default {
  name: "document",
  data() {
    return {
      docType: "",
      uuid: "",
      doc: {},
      isShow: false,
    };
  },
  components: {
    DocumentCover,
    DocumentContent,
  },
  methods: {
    getDoc() {
      axios
        .post(`${apiUrl}doc/getDoc`, {
          docType: this.docType,
          uuid: this.uuid,
        })
        .then((res) => {
          if (res.data.length !== 0) {
            this.doc = res.data[0];
            this.isShow = true;
          }
        });
    },
  },
  beforeMount() {
    this.docType = this.$route.params.docType;
    this.uuid = this.$route.params.uuid;
    this.getDoc();
  },
};
</script>