<template>
  <div>
    <h1 class="main-title">{{ mainTitle }}</h1>
    <div class="chunk" v-for="(item, index) in cards">
      <router-link :to="buildTo(item.docType, item.uuid)">
        <span class="chunk-title">{{ item.title }}</span>
      </router-link>
      <span class="chunk-subtitle">{{ item.subtitle }}</span>
      <span class="chunk-text">{{ item.text }}</span>
    </div>
  </div>
</template>

<script>
export default {
  name: "chunk",
  data() {
    return {
      mainTitle: "",
      cards: [],
    };
  },
  props: ["chunk"],
  methods: {
    buildTo(docType, uuid) {
      return `/doc/${docType}/${uuid}`;
    },
  },
  mounted() {
    this.mainTitle = this.chunk.mainTitle;
    this.cards = this.chunk.cards;
  },
};
</script>

<style scoped>
.main-title {
  border-left: solid 5px;
  padding-left: 10px;
  color: azure;
}
.chunk {
  background-color: #777777;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  margin: 20px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  color: azure;
}
.chunk-title {
  display: block;
  font-size: 120%;
  font-weight: bolder;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.chunk-subtitle {
  display: block;
  font-size: 50%;
  margin-top: 5px;
  font-weight: bold;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  opacity: 0.6;
}
.chunk-text {
  font-size: 80%;
  margin-top: 10px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
a {
  text-decoration: none;
  color: azure;
}
</style>