<template>
  <div class="card">
    <el-image :src="headImageUrl" :style="headImage"></el-image>
    <span class="nickname">{{ nickname }}</span>
    <span class="signature">{{ signature }}</span>
    <div class="image-link">
      <a v-for="(item, index) in iconList" :href="item.href">
        <el-image :src="item.src" :style="icon"></el-image>
      </a>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import settings from "../plugins/settings";
var apiUrl = settings.url.api;

export default {
  name: "personal-info",
  data() {
    return {
      headImageUrl: "",
      nickname: "",
      signature: "",
      iconList: settings.personalInfo.iconList,
      headImage: {
        borderRadius: "10px",
      },
      icon: {
        width: "24px",
        height: "auto",
      },
    };
  },
  methods: {
    getInfo() {
      axios.get(`${apiUrl}info`).then((res) => {
        this.headImageUrl = res.data[0].HeadImageUrl;
        this.nickname = res.data[0].Nickname;
        this.signature = res.data[0].Signature;
      });
    },
  },
  mounted() {
    this.getInfo();
  },
};
</script>

<style scope>
.card {
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  padding: 10px;
  background-color: #777777;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
  color: azure;
}
.nickname {
  display: block;
  text-align: center;
  font-weight: bolder;
  font-size: 100%;
  margin-top: 15px;
}
.signature {
  display: block;
  text-align: center;
  margin-top: 10px;
  font-size: 80%;
}
.image-link {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin-top: 20px;
}
</style>